<template>
  <div class="app">
    <el-container>
      <!-- 侧边栏 -->
      <el-aside
        class="app-side app-side-left"
        :class="isCollapse ? 'app-side-collapsed' : 'app-side-expanded'"
      >
        <Sidebar :collapse="isCollapse" :routes="$router.options.routes[1].children" />
      </el-aside>

      <el-container>
        <!-- 状态栏 -->
        <el-header class="app-header">
          <div style="width: 60px; cursor: pointer;" @click.prevent="isCollapse=!isCollapse">
            <i class="el-icon-s-grid"></i>
          </div>
          <el-menu
            default-active="/"
            router
            class="el-menu-demo tab-page"
            mode="horizontal"
            @select="handleSelect"
            background-color="#f28670"
            text-color="#ffffff"
            active-text-color="#ffffff"
          >
            <el-menu-item index="/">首页</el-menu-item>
          </el-menu>

          <div class="app-header-userinfo">
            <el-dropdown trigger="hover" :hide-on-click="false">
              <span class="el-dropdown-link" style="color:#ffffff">
                欢迎您，{{ $store.state.userInfo.userName }}
                <i class="el-icon-arrow-down"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item>我的消息</el-dropdown-item>
                <el-dropdown-item>设置</el-dropdown-item>
                <el-dropdown-item divided @click.native="logout">退出登录</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </el-header>
        <!-- 主内容 -->
        <el-main class="app-body">
          <template>
            <router-view></router-view>
          </template>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import Sidebar from "@/components/Sidebar";
export default {
  name: "Container",
  components: {
    Sidebar
  },
  inject: ["reload"],
  data() {
    return {
      isCollapse: false,
      loading: false //loading遮蔽层动画
    };
  },
  created() {
    // 已经为ajax请求设置了loading 请求前自动调用 请求完成自动结束
    // 添加请求拦截器
    this.$axios.interceptors.request.use(
      config => {
        console.log("拦截");
        // 在发送请求之前做些什么
        return config;
      },
      error => {
        // 对请求错误做些什么
        console.log("请求错误")
        return Promise.reject(error);
      }
    );
    // 添加响应拦截器
    this.$axios.interceptors.response.use(
      response => {
        // 可以在这里对返回的数据进行错误处理 如果返回的 code 不对 直接报错
        // 就可以省去在业务代码里重复判断
        // 例子
        // if (res.code != 0) {
        //     this.$Message.error(res.msg)
        //     return Promise.reject()
        // }
        const res = response;
        console.log(res);
        return res;
      },
      error => {
        // 对响应错误做点什么
        return Promise.reject(error);
      }
    );
  },
  mounted() {
    if (!JSON.parse(sessionStorage.getItem("isLogin")))
      this.$router.replace("/login");
  },
  methods: {
    logout: function() {
      this.$confirm("确认退出?", "提示", {})
        .then(() => {
          localStorage.clear();
          this.$router.push("/login");
        })
        .catch(() => {});
    },
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    }
  }
};
</script>

<style>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 198px;
  min-height: 400px;
}
.el-menu--vertical li:hover,.el-menu--vertical li.is-active{
  background-color: #f4f6f9 !important;
}
</style>



